.table-list {
    margin: 0 auto;
}

.table-list__cell {
    text-align: center;
}

.fa-times {
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
    color: rgba(197, 48, 48, 0.678);
}

.fa-times:before {
    content: "X";
    content: "\f00d";
}


